        <div v-else-if="getSuccess && !firstyflag" class="p-img content-middle use-red-packets">
            <div class="title"></div>
            <div class="tel">{{mobileNo | mobileNoSplit}}</div>
            <div class="info"> >  > </div>
            <div class="bottom"></div>
            <div class="btn-link">
                <router-link class="link" to="/app"></router-link>
            </div>
        </div>
        <div v-else class="p-img content-middle phone-verification">
            <div class="title"></div>
            <div class="content">
                <div class="tel">
                    <input class="input" type="tel" placeholder="" v-model="mobileNo" maxlength="11" />
                </div>
                <div class="verification">
                    <input class="get a" type="button" v-if="countdown" @click="getVerificationCode" value="" />
                    <input class="get b" type="button" v-else :value="count" />
                    <input class="input" type="text" v-model="msgCode" maxlength="6" placeholder="" />
                </div>
            </div>
            <div class="bottom">
                <a href="javascript:void(0);" @click="getVoiceVerificationCode"></a>
            </div>
            <div class="btn-link">
                <a class="link" href="javascript:void(0)" @click="getRedPacket"></a>
            </div>
        </div>
        <div class="p-img"><img src="@/assets/images/my-red-packets_05.png"></div>
        <div class="p-img"><img src="@/assets/images/my-red-packets_06.png"></div>
