<template>
    <div class="app">
        <div class="return-result-icon">
            <svg class="icon blue" aria-hidden="true">
                <use xlink:href="#icon-dui1"></use>
            </svg>
        </div>
        <div class="return-result-title"></div>
        <div class="return-result-info">
            1000
        </div>
        <div class="return-result-info">
            <span class="blue">{{count}}S</span>
        </div>
        <router-link to="/app" class="link-btn-orange" style="margin-top:1rem;" ></router-link>
    </div>
</template>

<script>
import '@/assets/css/app.css'
export default {
    name: 'AddSucess',
    data(){
        return{
            count:3,//
        }
    },
    created(){
        this.toCount();
    },
    methods:{
        //
        toCount(){
            let timer = setInterval(() => {
                if (this.count > 0) {
                    this.count--;
                }
                else{
                    clearInterval(timer);
                    this.$router.push({name:'app'});
        }
        }, 1000)
        }
    }
}
</script>
