<template>
    <div class="app">
        <div class="validation-code-top">
            
        </div>
       <div class="form-input-list">
            <div class="item">
                <span class="t"></span>
                <div class="i"><input type="text" placeholder="" v-model="mobileNo" maxlength="11"></div>
            </div>
            <div class="item">
                <span class="t"></span>
                <div class="i"><input type="text" placeholder=""  v-model="msgCode" maxlength="6"></div>
                <a class="r-l"  v-if="countdown" @click="getVerificationCode" href="javascript:void(0);"></a>
                <span v-else class="r-l">{{count}}</span>
            </div>
            <div class="agreement">
                <a class="orange"  @click="getVoiceVerificationCode" href="javascript:void(0);"></a>
            </div>
       </div>
        <a class="link-btn-orange" style="margin-top:1rem;"  href="javascript:void(0)" @click="bindConfirm"></a>
  
        
    </div>
</template>

<script>
import '@/assets/css/app.css'
export default {
    name: 'PayApplication',
    data(){
        return{
            mobileNo:"",
            isSend:true,   //
            msgCode:"",//
            smsFlow:"",//
            count: '60',//
            countdown:true,//
            tokenId: this.$store.state.token
        }
    },
    created(){

                
    },
    methods:{




        //
        getVerificationCode(){
           this.countdown = false;
           this.$util.openIndicator();
           this.$util.postRequest('/walletpn/login/sentMessage',{tokenId:this.tokenId,smsType:0,mobileNo:this.mobileNo})
            .then(res => {
                console.log(res);
                if(res == undefined || null == res){
                    this.countdown = true;
                    return;
                }

                this.isSend = true;
                this.smsFlow=res.data.smsFlow;
                this.count = 60;
                let timer = setInterval(() => {
                    if (this.count > 0) {
                        this.count--;
                    }
                    else{
                        this.countdown = true;
                        clearInterval(timer);
            }
            }, 1000)
                });

        },

        //
        getVoiceVerificationCode(){
            this.$util.openIndicator();
           this.$util.postRequest('/walletpn/login/sentMessage',{tokenId:this.tokenId,smsType:2,mobileNo:this.mobileNo})
            .then(res => {
                console.log(res);
                if(res == undefined || null == res){
                    return;
                }
                
                this.smsFlow=res.data.smsFlow;
                this.isSend = true;
            });
        },
        //
        bindConfirm(){
            if(!this.$util.isValidate(this.mobileNo)){
                Toast({
                    message:'!',
                    duration:3000
                });
                return;
            }
            if(!this.$util.isValidate(this.msgCode)){
                Toast({
                    message:'!',
                    duration:3000
                });
                return;
            }
            this.$util.openIndicator();
             this.$util.postRequest('/walletpn/login/bindUser',{referrerTokenId:localStorage.getItem('referrerTokenId'),tokenId:this.tokenId,smsFlow:this.smsFlow,msgCode:this.msgCode,mobileNo:this.mobileNo})
            .then(res => {
                console.log(res);
                if(res == undefined || null == res){
                    return;
                }
                localStorage.removeItem('referrerTokenId');
                this.$router.push({name:'app'});
            });
        }
    }
}
</script>
