<template>
    <div class="app">
        <div class="add-bank-card-top">
            
        </div>
       <div class="form-input-list">
            <div class="item">
                <span class="t"></span>
                <div class="i"><input type="text" placeholder="" v-model="name" :readonly="nameDisable"></div>
            </div>
            <div class="item">
                <span class="t"></span>
                <div class="i"><input type="tel" placeholder="" v-model="cardShow" @keyup="getCardCode" maxlength="23"></div>
                <input v-model="cardNo" type="hidden">
            </div>
            <div class="agreement">
                
                <a class="orange"  :href="this.$dataUrl+'/tlqbh5/bank/querySupportBankList?appId='+appId+'&mchtId='+mchtId"></a>
               <!--
                <a class="orange"  :href="'http://test.allinpay.com/tlqbh5/bank/querySupportBankList?appId='+appId+'&mchtId='+mchtId"></a>
                 -->
            </div>
       </div>
        <a class="link-btn-orange" style="margin-top:1rem;" href="javascript:void(0)" @click="conf"></a>
    </div>
</template>

<script>
import '@/assets/css/app.css'
import { Toast } from 'mint-ui'
export default {
    name: 'AddCard',
    data(){
        return{
            name:"",//
            authenticated:"0",// 0-1-
            cardNo:"",//
            cardShow:"",//
            nameDisable:false, //
            pidCode:"",//
            appId:"",//appId
            mchtId:""//
        }
    },
    created(){
        this.queryUserInfo();
    },
    methods:{
        //
        queryUserInfo(){
            this.$util.postRequest('/walletpn/sign/queryUserInfo',{tokenId:this.$store.state.token})
            .then(res =>{
                console.log(res);
                this.name = res.data.realName;
                this.pidCode = res.data.pidCodeMark;
                this.authenticated = res.data.authenticated;
                this.appId=res.data.appId;
                this.mchtId=res.data.mchtId;
                if(this.authenticated=='1'){
                    this.nameDisable=true;
                }
            })
        },

        //
        conf(){
            //
            if(!this.$util.isValidate(this.name)){
                Toast({
                    message:'!',
                    duration:3000
                });
                return;
            }
            //
            if(!this.$util.isValidate(this.cardNo)){
                Toast({
                    message:'!',
                    duration:3000
                });
                return;
            }
            if(this.cardNo.length<15){
                Toast({
                    message:'!',
                    duration:3000
                });
                return;
            }
            if(this.cardNo.length>19){
                Toast({
                    message:'!',
                    duration:3000
                });
                return;
            }

            this.$util.postRequest('/walletpn/sign/addCard',{tokenId:this.$store.state.token,name:this.name,cardNo:this.cardNo})
            .then(res =>{
                console.log(res);
                if(res == undefined){
                    return;
                }
                
                this.$router.push({ name: 'addMobile'});
            })
        },
        
        getCardCode(){
            if(/\S{5}/.test(this.cardShow)){
                this.cardShow = this.cardShow.replace(/\s/g, '').replace(/(.{4})/g, "$1 ");
            }
            this.cardNo = this.cardShow.replace(/\s+/g,"")
            console.log(this.cardNo)
        },
    }
}
</script>
