<template>
<div>
    <div class="renew-top-info">
        
    </div>
    <div class="renew-item">
        <span class="i"><img src="@/assets/images/2018-10-23_03.png"></span>
        <span class="t">
            <b></b>
            <span class="n"><span class="b">2</span>/</span>
            <span class="g">10/</span>
        </span>
    </div>
    <a class="link-btn-orange" style="margin-top:2.5rem;" href="#" @click="toRenew"></a>
    <a class="link-btn-gray" style="margin-top:.5rem;" :href="'/walletpn/products/pay?issuerId='+issuerId+'&productNo='+productNo+ '&discountRatio='+discountRatio"  ></a>
    
    <div class="renew-bottom-info">
       <a @click="share = true"> </a>
    </div>
    
    <!--
    <form ref="myBankForm" :action="renewParam.requestURL" target="_blank">
    -->
    <form  v-if="renewParam"  submit="" ref="payForm" :action="renewParam.requestURL" >
        <input type="hidden" :key="key" :value="val" v-if="key!='requestURL'" :name="key" v-for="(val,key) in renewParam"/>
    </form>
     <!--  -->
    <div v-if="share" class="popup-share">
        <div class="icon-share">
            <span></span>
        </div>
        <div @click="share = false" class="bg-content"></div>
    </div>
</div>
</template>

<script>
import '@/assets/css/app.css'
import wx from 'weixin-js-sdk'
export default {
    name: 'renewalDate',
    data(){
        return{
            renewParam:{},
            wxConfParam:{},
            wxSharaParam:{},
            share: false,
            thumb: require ('@/assets/logo.png'),
            issuerId:"",//
            productNo:"",//
            discountRatio:10 //,100950.05
        }
    },
    created(){
        //
        this.issuerId = this.$route.query.issuerId;
        this.productNo = this.$route.query.productNo;

        // alert("window.location.href:"+window.location.href);
        //
        this.$util.postRequest('/walletpn/invite/wxConfig')
                .then( res => {
                    // console.log(res)
                    
                    this.wxConfParam = JSON.parse(JSON.stringify(res.data));

                    if( res.data == undefined){
                        return;
                    }
                    
                    wx.config({
                        debug: false,
                        appId: this.wxConfParam.appId, // Ticke------
                        timestamp:this.wxConfParam.timestamp, // 
                        nonceStr: this.wxConfParam.nonceStr, // 
                        signature: this.wxConfParam.signature,// 1
                        //:
                        jsApiList: [
                        'onMenuShareAppMessage','onMenuShareTimeline'
                        ]
                    });

                    let imgUrl = location.protocol + location.host + this.thumb;
                    //
                    this.$util.postRequest('/walletpn/invite/wxShare')
                        .then( res => {
                                console.log(res)
                        this.wxSharaParam = res.data;
                    
                    });

                    wx.ready(()=>{

                            
                            
                            //
                            wx.onMenuShareAppMessage({
                                title: this.wxSharaParam.title, // 
                                desc: this.wxSharaParam.desc, // 
                                link: this.wxSharaParam.link, // JS
                                imgUrl: imgUrl, // 
                                //type: this.wxSharaParam.type, // ,musicvideolinklink
                                //dataUrl: this.wxSharaParam.dataUrl, // typemusicvideo
                                success: function (res) {
                                    // 
                                    console.log(":",res);
                                }
                            });


                            //
                            wx.onMenuShareTimeline({
                                title: this.wxSharaParam.title, // 
                                desc: this.wxSharaParam.desc, // 
                                link: this.wxSharaParam.link, // JS
                                imgUrl: imgUrl, // 
                                //type: this.wxSharaParam.type, // ,musicvideolinklink
                                // dataUrl: this.wxSharaParam.dataUrl, // typemusicvideo
                                success: function (res) {
                                    // 
                                    console.log(":",res);
                                }
                            });

                        });
                });

    },
    methods:{

        //
        async toRenew(){
            let result =false;
            await this.$util.postRequest('/walletpn/renew/renew',{amount:"100"})
                .then( res => {
                    console.log(res)
                    this.renewParam = res.data;
                    
                    if(res.data == undefined || res.data.errorCode!=undefined){
                        result=false;
                    }else{
                        result = true;
                    }
                });

                if( result){
                    //form
                    this.$refs.payForm.submit();
                }
                
        },

    }
}
</script>
