<template>
    <div class="app">
        <div class="validation-code-top">
            <br> 
            {{mobileNoMark}}
        </div>
       <div class="form-input-list">
            <div class="item">
                <span class="t"></span>
                <div class="i"><input ref="msgCode" type="tel" placeholder=""  v-model="msgCode" maxlength="6"></div>
                <a class="r-l" v-if="countdown" @click="getVerificationCode" href="javascript:void(0);"></a>
                <span v-else class="r-l">{{count}}</span>
            </div>
            <div class="agreement">
                <a class="orange"  @click="getVoiceVerificationCode" href="javascript:void(0);"></a>
            </div>
       </div>
        <a class="link-btn-orange" style="margin-top:1rem;"  href="javascript:void(0)" @click="payConfirm"></a>
  
        
    </div>
</template>

<script>
import '@/assets/css/app.css'
export default {
    name: 'PayApplication',
    data(){
        return{
            mobileNoMark:"",
            isSend:true,   //
            msgCode:"",//
            count: '',//
            countdown:false,//
            tokenId: this.$store.state.token
        }
    },
    created(){
        this.queryPaymentOrder();
                this.count = 60;
                let timer = setInterval(() => {
                    if (this.count > 0) {
                        this.count--;
                    }
                    else{
                        this.countdown = true;
                        clearInterval(timer);
            }
            }, 1000)
                
    },
    methods:{

        //
        queryPaymentOrder(){
            this.$util.postRequest('/walletpn/payment/queryOrder',{tokenId:this.tokenId})
            .then(res =>{
                console.log(res);
                if(res == undefined || null == res){
                    return;
                }
                this.mobileNoMark = res.data.ylMobileNo;
            })
        },


        //
        getVerificationCode(){
           this.countdown = false;
            this.$util.openIndicator();
           this.$util.postRequest('/walletpn/payment/application',{tokenId:this.tokenId,smsType:'0'})
            .then(res => {
                console.log(res);
                if(res == undefined || null == res){
                    this.countdown = true;
                    return;
                }

                this.isSend = true;
            
                this.count = 60;
                let timer = setInterval(() => {
                    if (this.count > 0) {
                        this.count--;
                    }
                    else{
                        this.countdown = true;
                        clearInterval(timer);
            }
            }, 1000)
                });
                this.$refs.msgCode.focus()

        },

        //
        getVoiceVerificationCode(){
            this.$util.openIndicator();
           this.$util.postRequest('/walletpn/payment/application',{tokenId:this.tokenId,smsType:'2'})
            .then(res => {
                console.log(res);
                if(res == undefined || null == res){
                    return;
                }
                this.isSend = true;
            });
        },
        //
        payConfirm(){
            if(!this.$util.isValidate(this.msgCode)){
                Toast({
                    message:'!',
                    duration:3000
                });
                return;
            }
            this.$util.openIndicator();
             this.$util.postRequest('/walletpn/payment/confirm',{tokenId:this.tokenId,msgCode:this.msgCode})
            .then(res => {
                console.log(res);
                if(res == undefined || null == res){
                    return;
                }
                this.$router.push({name:'paySuccess'});
            });
        }
    }
}
</script>
