<template>
<div>
    <div class="img invite-friends-img">
        <router-link class="a" to="/myInvitation"></router-link>
        <img src="@/assets/images/2018-10-23-2_01.png">
    </div>
    <div class="img invite-friends-img">
        <span class="s"></span>
        <div class="j">
            
            <span class="n">{{getData.savedAmt}}</span>
            
        </div>
        <span class="time">{{getData.validDateNumber}}</span>
        <img src="@/assets/images/2018-10-23-2_02.png">
    </div>
    <div class="img invite-friends-img">
        <div class="info">
             1VIP7 ! <br>
             30
        </div>
        <img src="@/assets/images/2018-10-23-2_03.png">
    </div>
    <div class="img invite-friends-img">
        <a @click="share = true" class="btn-1" href="#"></a>
        <a @click="getQrCode()" class="btn-2" href="#"></a>
        <img src="@/assets/images/2018-10-23-2_04.png">
    </div>
     <!--  -->
    <div v-if="share" class="popup-share">
        <div @click="share = false" class="img-share">
            <span></span>
        </div>
        <!-- <div class="icon-share">
            <span></span>
        </div> -->
        <div class="bg-content"></div>
    </div>
    <!--  -->
    <div v-if="showQrCode" class="popup-qr-code">
        <div class="main-content">
            <span @click="showQrCode = false" class="close"></span>
            <div class="t">
                
            </div>
            <div class="i">
                <img :src="qrImg">
            </div>
        </div>
        <div class="bg-content"></div>
    </div>
</div>
</template>

<script>
import QRCode from 'qrcode'
import wx from 'weixin-js-sdk'
export default {
    data(){
        return {
            qrImg: '',
            showQrCode: false,
            share: false,
            thumb: require ('@/assets/logo.png'),
            getData: '',
            detail: ''
        }
    },
    methods:{
        getQrCode(){
            this.$util.postRequest('/walletpn/invite/f2fInvite')
            .then( res => {
                console.log(res)
                QRCode.toDataURL(res.data.inviteUrl, {
                    errorCorrectionLevel: 'M',
                    width: 300,
                    type: 'image/jpeg',
                    rendererOpts: {
                        quality: 0.9
                    }
                })
                .then(url => {
                    this.qrImg = url
                    this.showQrCode = true
                })
            })
        },
        goToWX(){
            this.$util.postRequest('/walletpn/invite/wxConfig')
            .then( response => {
                let res = JSON.parse(JSON.stringify(response))
                res = res.data;
                this.wxInit(res);
                console.log

                console.log(res)
            })
        },
        getDitall(){
            this.$util.postRequest('/walletpn/invite/wxShare')
            .then( response => {
                console.log(1)
                console.log(response)
                this.detail = response.data
                this.goToWX()
            })
        },
        // 
        wxInit(res) {
            let imgUrl = location.protocol + location.host + this.thumb;
            wx.config({
                debug: false,
                appId: res.appId,
                timestamp: res.timestamp,
                nonceStr: res.nonceStr,
                signature: res.signature,
                jsApiList: ['onMenuShareTimeline', 'onMenuShareAppMessage', 'onMenuShareQQ', 'onMenuShareQZone']
            });
            wx.ready(()=> {
                //
                wx.onMenuShareTimeline({
                    title: this.detail.title, // 
                    desc: this.detail.desc, // 
                    link: this.detail.link, // 
                    imgUrl: imgUrl, // 
                    success: function() {
                        // alert("")
                    },
                    cancel: function() {
                        alert(",!")
                    }
                }); 
                //
                wx.onMenuShareAppMessage({
                    title: this.detail.title, // 
                    desc: this.detail.desc, // 
                    link: this.detail.link, // 
                    imgUrl: imgUrl, // 
                    success: function() {
                        // alert("")
                    },
                    cancel: function() {
                        alert(",!")
                    }
                });
                //QQ
                wx.onMenuShareQQ({
                    title: this.detail.title, // 
                    desc: this.detail.desc, // 
                    link: this.detail.link, // 
                    imgUrl: imgUrl, // 
                    success: function() {
                        // alert("QQ")
                    },
                    cancel: function() {
                        alert(",!")
                    }
                });
                //QQ
                wx.onMenuShareQZone({
                    title: this.detail.title, // 
                    desc: this.detail.desc, // 
                    link: this.detail.link, // 
                    imgUrl: imgUrl, // 
                    success: function() {
                        // alert("QQ")
                    },
                    cancel: function() {
                        alert(",!")
                    }
                });
            });
            wx.error(function(err) {
                alert(JSON.stringify(err))
            });
        },
        getInfo(){
            this.$util.postRequest('/walletpn/LoginEnter/firstPageData')
            .then( res => {
                // console.log(res)
                this.getData = res.data
            })
        }
    },
    computed: {
        
    },
    created(){
        this.getDitall()
        this.getInfo()
    }
}
</script>
