<template>
<div style="height: 100%;">
    <div class="transaction-details-list" style="overflow:scroll; height: 100%; -webkit-overflow-scrolling : touch;">
       <mt-loadmore bottomPullText="" :top-method="loadTop" :bottom-method="loadBottom" :bottom-all-loaded="allLoaded" ref="loadmore" :auto-fill="false"> 
        <div v-for="(v,i) in itemList" :key="i" class="item">
            <div class="t">{{v.createDatetime}}
                <span class="p">{{v.amount}}</span>
            </div>
            <div class="n">
                
                <span class="y">{{v.discount}}</span>
            </div>
        </div>
        <div v-if="itemList.length < 1" class="index-bottom-info">
            <div class="t"></div>
        </div>
       </mt-loadmore>
    </div>

</div>
</template>

<script>
import { Loadmore } from 'mint-ui';
export default {
  name: 'transactionDetails',
    data(){
        return {
         pageData:{
                    currentPage:1,    //currentPage  
                    typeCode:'0'        //0123
                },
                itemList:[],
                allLoaded:false//
            }
       
},
    methods:{
           loadTop(){
                // 
                console.log('');
                this.$refs.loadmore.onTopLoaded();
            },
            loadBottom() {
                // 
                console.log('');
                this.pageData.currentPage++;
                this.getInfo();
                this.$refs.loadmore.onBottomLoaded();
            },
        getInfo(){
            const num = 10;//
            this.$util.postRequest('/walletpn/transDetail/queryTransDetail',{pageIndex:this.pageData.currentPage,pageSize:num})
            .then( res => {
                console.log(res)
                this.itemList = this.itemList.concat(res.data.dataList);
                if(res.data.dataList.length<num){
                         this.allLoaded = true;// 
                    } 
            })
            .catch(function (error) {
                console.log(error);
            });
        }
    },
    created(){
        this.getInfo();

    }
}
</script>
