<template>
    <div class="app">
        <div class="validation-code-top">
            <br>
        </div>
        <div class="validation-code-tel">
            {{mobileNoMark}}
        </div>
       <div class="form-input-list">
            <div class="item">
                <span class="t"></span>
                <div class="i"><input ref="msgCode" type="tel" placeholder="" v-model="msgCode" maxlength="6"></div>
                <a v-if="countdown" @click="getVerificationCode" class="r-l" href="javascript:void(0);"></a>
                <span v-else class="r-l">{{count}}</span>
            </div>
       </div>
        <a class="link-btn-orange" style="margin-top:1rem;" href="javascript:void(0)" @click="conf"></a>
  
        
    </div>
</template>

<script>
import '@/assets/css/app.css'
import { Toast } from 'mint-ui'
export default {
    name: 'AddMsgCode',
    data(){
        return{
            mobileNoMark:"",
            isSend:false,   //
            msgCode:"",//
            count: '',//
            countdown:true,//
        }
    },
    created(){
        this.querySignInfo();
    },
    methods:{
        //
        querySignInfo(){
            this.$util.postRequest('/walletpn/sign/querySignInfo',{tokenId:this.$store.state.token})
            .then(res =>{
                console.log(res);
                if(res == undefined || null == res){
                    return;
                }
                this.mobileNoMark = res.data.ylMobileNo;
            })
        },

        //
        getVerificationCode(){
            this.$util.openIndicator();
            this.$util.postRequest('/walletpn/sign/signApply',{tokenId:this.$store.state.token})
            .then(res => {
                console.log(res);
                if(res == undefined || null == res){
                    return;
                }

                this.isSend = true;
            
                this.count = 60;
                this.countdown = false;
                let timer = setInterval(() => {
                    if (this.count > 0) {
                        this.count--;
                    }
                    else{
                        this.countdown = true;
                        clearInterval(timer);
            }
            }, 1000)
            });
            this.$refs.msgCode.focus()
        },

        //
        conf(){
            if(!this.$util.isValidate(this.msgCode)){
                Toast({
                    message:'!',
                    duration:3000
                });
                return;
            }
            this.$util.openIndicator();
            this.$util.postRequest('/walletpn/sign/signConf',{tokenId:this.$store.state.token,msgCode:this.msgCode})
            .then(res => {
                console.log(res);
                if(res == undefined || null == res){
                    return;
                }
                this.$router.push({name:'addSucess'});
            });
        }
    }
}
</script>
