<template>
<div style="height: 100%;">
    <div class="order-transaction-details-list" style="overflow:scroll; height: 100%; -webkit-overflow-scrolling : touch;">
       <mt-loadmore bottomPullText="" :top-method="loadTop" :bottom-method="loadBottom" :bottom-all-loaded="allLoaded" ref="loadmore" :auto-fill="false"> 
        <div v-for="(v,i) in itemList" :key="i" class="item">
            <div class="sj">
                <span class="l" v-if="v.transType=='4'"></span>
                <span class="l" v-else>{{v.issuerName}}</span>
                <span class="r">{{v.amount}}</span>
            </div>
            <div class="yh">{{v.discount}}</div>
            <div class="yh red" v-if="v.deducteAmt!= null && v.deducteAmt!='0.00' ">{{v.deducteAmt}}</div>
            <div class="ddh">{{v.orderId}}</div>
            <div class="time">
                <span class="l">{{v.createDatetime}}</span>
                <span class="r" v-if="v.state=='1'"></span>
                <span class="r" v-if="v.state=='2'"></span>
                <span class="r" v-if="v.state=='3'"></span>
                <span class="r" v-if="v.state=='4'"></span>
                <span class="r" v-if="v.state=='5'"></span>
                <span class="r" v-if="v.state=='6'"></span>
            </div>
        </div>
        <div v-if="itemList.length < 1" class="index-bottom-info">
            <div class="t"></div>
        </div>
       </mt-loadmore>
    </div>

</div>
</template>

<script>
import { Loadmore } from 'mint-ui';
export default {
  name: 'transactionDetails',
    data(){
        return {
         pageData:{
                    currentPage:1,    //currentPage  
                    typeCode:'0'        //0123
                },
                itemList:[],
                allLoaded:false//
            }
       
},
    methods:{
           loadTop(){
                // 
                console.log('');
                this.$refs.loadmore.onTopLoaded();
            },
            loadBottom() {
                // 
                console.log('');
                this.pageData.currentPage++;
                this.getInfo();
                this.$refs.loadmore.onBottomLoaded();
            },
        getInfo(){
            const num = 10;//
            this.$util.postRequest('/walletpn/transDetail/queryTransDetail2',{pageIndex:this.pageData.currentPage, pageSize:num, tokenId: this.$store.state.token})
            .then( res => {
                console.log(res)
                this.itemList = this.itemList.concat(res.data.dataList);
                if(res.data.dataList.length<num){
                         this.allLoaded = true;// 
                    } 
            })
            .catch(function (error) {
                console.log(error);
            });
        }
    },
    created(){
        this.getInfo();

    }
}
</script>
