<template>
    <div class="app">
        <div class="my-red-packets-p">
            <div class="p-img">
                <div class="banner-info">
                    <div class="t"></div>
                    <div class="i"><b></b><b></b><b>APP</b></div>
                </div>
                <img src="@/assets/images/my-red-packets_19.png">
            </div>
        </div>
       <div class="my-red-packets-input-list">
            <div class="item">
                <span class="t"></span>
                <div class="i">
                    <input type="text" v-model="mchtInfo" maxlength="20" placeholder="">
                    <div class="prompt">20</div>
                </div>
                
            </div>
            <div class="item">
                <span class="t"></span>
                <div class="i"><input type="text" placeholder=""  v-model="mobileNo" maxlength="11" ></div>
            </div>
            <div class="item">
                <span class="t"></span>
                <div class="i"><input type="text" placeholder="" v-model="msgCode" maxlength="6" ></div>
                <a class="r-l"  v-if="countdown" @click="getVerificationCode" href="javascript:void(0);" ></a>
                <a class="r-l" v-else>{{count}}</a>
            </div>
            <div class="agreement">
                <a href="javascript:void(0);" @click="getVoiceVerificationCode"></a>
            </div>
       </div>
       <a class="link-btn-orange" style="margin-top:.6rem;" href="javascript:void(0)" @click="getRedPacket"></a>
       <div class="renew-bottom-info"></div>
    </div>
</template>

<script>
import '@/assets/css/app.css'
export default {
    data(){
        return {
            mchtInfo:"",
            mobileNo:"",
            isSend:true,   //
            msgCode:"",//
            smsFlow:"",//
            count: '60',//
            countdown:true,//
            getSuccess:false,
            tokenId: ''
        }
    },
    methods:{ //
       
       //
        getVerificationCode(){

            if(!this.$util.isValidate(this.mchtInfo)){
                Toast({
                    message:'!',
                    duration:3000
                });
                return;
            }
           this.countdown = false;
           this.$util.openIndicator();
           this.$util.postRequest('/walletpn/cusMchtInfo/sentMessage',{tokenId:this.tokenId,smsType:0,mobileNo:this.mobileNo})
            .then(res => {
                console.log(res);
                if(res == undefined || null == res){
                    this.countdown = true;
                    return;
                }

                this.isSend = true;
                this.smsFlow=res.data.smsFlow;
                this.count = 60;
                let timer = setInterval(() => {
                    if (this.count > 0) {
                        this.count--;
                    }
                    else{
                        this.countdown = true;
                        clearInterval(timer);
            }
            }, 1000)
                });

        },

        //
        getVoiceVerificationCode(){

            if(!this.$util.isValidate(this.mchtInfo)){
                Toast({
                    message:'!',
                    duration:3000
                });
                return;
            }
            this.$util.openIndicator();
           this.$util.postRequest('/walletpn/cusMchtInfo/sentMessage',{tokenId:this.tokenId,smsType:2,mobileNo:this.mobileNo})
            .then(res => {
                console.log(res);
                if(res == undefined || null == res){
                    return;
                }
                
                this.smsFlow=res.data.smsFlow;
                this.isSend = true;
            });
        },
       saveTokenId(){
            if(this.$route.query.tokenId){
                this.tokenId = this.$route.query.tokenId;

                sessionStorage.setItem("tokenId",this.tokenId);
                window.location.replace("/walletpn/vueapp/#/addMchtInfo");
                //this.$router.replace({name:'addMchtInfo'})
            }else{

                this.tokenId = sessionStorage.getItem("tokenId");
            }
        },
        //
        getRedPacket(){
            if(!this.$util.isValidate(this.mobileNo)){
                Toast({
                    message:'!',
                    duration:3000
          
          });
                return;
            }
            if(!this.$util.isValidate(this.msgCode)){
                Toast({
                    message:'!',
                    duration:3000
                });
                return;
            }
            this.$util.openIndicator();
             this.$util.postRequest('/walletpn/cusMchtInfo/addMchtInfo',{tokenId:this.tokenId,smsFlow:this.smsFlow,msgCode:this.msgCode,mobileNo:this.mobileNo,mchtInfo:this.mchtInfo})
            .then(res => {
                console.log(res);
                if(res == undefined || null == res){
                    return;
                }
                
                this.$router.push({name:'addMchtSucc',params:{mobileNo:this.mobileNo}});
            });
        }
    },
    computed:{

    },
    created(){
        this.saveTokenId();
    }
}
</script>