<template>
<div class="my-red-packets-p">
        <div class="p-img">
            <router-link to="/myRewardList" class="invitation-my-link"></router-link>
            <img src="@/assets/images/my-red-packets-bg_01.png">
        </div>
        <div class="p-img"><img src="@/assets/images/my-red-packets-bg_02.png"></div>
        <div class="p-img"><img src="@/assets/images/my-red-packets-bg_03.png"></div>
        <div class="p-img"><img src="@/assets/images/my-red-packets-bg_04.png"></div>
        <div class="p-img"><img src="@/assets/images/my-red-packets-bg_05.png"></div>
        <div v-if="actState=='1'">
            <div class="p-img">
                <div class="invitation-btn-link">
                    <a class="btn1" @click="share = true"  href="javascript:void(0)"></a>
                </div>
                <img src="@/assets/images/my-red-packets-bg_06.png">
            </div>
            <div class="p-img">
                <div class="invitation-btn-link">
                    <a @click="getQrCode" class="btn2" href="javascript:void(0)"></a>
                    <router-link to="/activityRules" class="link" > </router-link>
                </div>
                <img src="@/assets/images/my-red-packets-bg_07.png">
            </div>
        </div>
        <div v-else>
            <div class="p-img">
                <div class="invitation-btn-link">
                    <a class="btn1" href="javascript:void(0)"></a>
                </div>
                <img src="@/assets/images/my-red-packets-bg_06.png">
            </div>
        </div>


        <!--  -->
        <div v-if="share" class="popup-share">
            <div @click="share = false" class="img-share">
                <span></span>
            </div>
            <!-- <div class="icon-share">
                <span></span>
            </div> -->
            <div class="bg-content"></div>
        </div>
        <!--  -->
        <div v-if="showQrCode" class="popup-qr-code">
            <div class="main-content">
                <span @click="showQrCode = false" class="close"></span>
                <div class="t">
                    
                </div>
                <div class="i">
                    <img :src="qrImg">
                </div>
            </div>
            <div class="bg-content"></div>
        </div>
    </div>
</template>

<script>
import QRCode from 'qrcode'
import wx from 'weixin-js-sdk'
export default {
    data(){
        return {
            tokenId: this.$store.state.token,
            qrImg: '',
            showQrCode: false,
            share: false,
            thumb: require ('@/assets/logo.png'),
            detail: '',
            actState:"0",//0- 1-
        }
    },
    methods:{
        getQrCode(){
            this.$util.postRequest('/walletpn/cusInvite/f2fInvite',{tokenId:this.tokenId,queryType:'0'})
            .then( res => {
                console.log(res)
                QRCode.toDataURL(res.data.inviteUrl, {
                    errorCorrectionLevel: 'M',
                    width: 300,
                    type: 'image/jpeg',
                    rendererOpts: {
                        quality: 0.9
                    }
                })
                .then(url => {
                    this.qrImg = url
                    this.showQrCode = true
                })
            })
        },
        goToWX(){
            this.$util.postRequest('/walletpn/cusInvite/wxConfig',{tokenId:this.tokenId})
            .then( response => {
                let res = JSON.parse(JSON.stringify(response))
                res = res.data;
                this.wxInit(res);

                console.log(res)
            })
        },
        getDitall(){
            this.$util.postRequest('/walletpn/cusInvite/wxShare',{tokenId:this.tokenId,queryType:'0'})
            .then( response => {
                console.log(1)
                console.log(response)
                this.detail = response.data
                this.goToWX()
            })
        },
        // 
        wxInit(res) {
            let imgUrl = location.protocol + location.host + this.thumb;
            wx.config({
                debug: false,
                appId: res.appId,
                timestamp: res.timestamp,
                nonceStr: res.nonceStr,
                signature: res.signature,
                jsApiList: ['onMenuShareTimeline', 'onMenuShareAppMessage', 'onMenuShareQQ', 'onMenuShareQZone']
            });
            wx.ready(()=> {
                //
                wx.onMenuShareTimeline({
                    title: this.detail.title, // 
                    desc: this.detail.desc, // 
                    link: this.detail.link, // 
                    imgUrl: imgUrl, // 
                    success: function() {
                        // alert("")
                    },
                    cancel: function() {
                        alert(",!")
                    }
                }); 
                //
                wx.onMenuShareAppMessage({
                    title: this.detail.title, // 
                    desc: this.detail.desc, // 
                    link: this.detail.link, // 
                    imgUrl: imgUrl, // 
                    success: function() {
                        // alert("")
                    },
                    cancel: function() {
                        alert(",!")
                    }
                });
                //QQ
                wx.onMenuShareQQ({
                    title: this.detail.title, // 
                    desc: this.detail.desc, // 
                    link: this.detail.link, // 
                    imgUrl: imgUrl, // 
                    success: function() {
                        // alert("QQ")
                    },
                    cancel: function() {
                        alert(",!")
                    }
                });
                //QQ
                wx.onMenuShareQZone({
                    title: this.detail.title, // 
                    desc: this.detail.desc, // 
                    link: this.detail.link, // 
                    imgUrl: imgUrl, // 
                    success: function() {
                        // alert("QQ")
                    },
                    cancel: function() {
                        alert(",!")
                    }
                });
            });
            wx.error(function(err) {
                alert(JSON.stringify(err))
            });
        },

        //
        queryMchtAct(){
            this.$util.postRequest('/walletpn/reward/queryMchtAct', {tokenId:this.tokenId})
            .then( res => {
                console.log(res);
                if(res == undefined){
                    return;
                }

                this.actState = res.data.actState;
            });
        },
  
    },
    computed: {
        
    },
    created(){
        this.getDitall();
        this.queryMchtAct();
    }
}
</script>
