<template>
  <div>
    <div class="img index-header">
        <div class="s">
            <span class="t"> </span>
            ({{getData.validDateNumber}})
        </div>
        <div class="j">
              
            <span class="n">{{getData.savedAmt}}</span>
            
        </div>
        <router-link class="a" to="/transactionDetails"> ></router-link>
        <router-link class="img-a" to="/goodFriendsShare"></router-link>
        <img src="@/assets/images/2018_10_22_02.png">
    </div>
    <div class="select-merchants-module">
        <div class="title-content">
            <span class="i"></span>
            <span class="l"></span>
            <span class="r"></span>
        </div>
        <div class="main-content">
            <div v-for="(v,i) in getData.dataList" :key="i" class="item">
                <span class="i"><img :src="v.imgPath"></span>
                <span class="t">{{v.name}}<span class="n"><span class="b">{{v.discountNuber}}</span></span></span>
                <span v-if="v.issuerId=='WALM'" class="orangeColor">20</span>
                <router-link v-if="getData.state == '2'" class="a" :to="{ path: 'renewalDate', query: { issuerId: v.issuerId, productNo: v.productNo }}"></router-link>
                <a v-else class="a" :href="'/walletpn/products/pay?issuerId=' + v.issuerId +'&productNo=' + v.productNo + '&discountRatio=' + v.discount"></a>
            </div>
        </div>
        <div class="bottom-content"><span class="t"></span></div>
    </div>
    <div class="index-bottom-info">
        95193
        <div class="t"></div>
    </div>
    <!--  -->
    <div v-if="getData.firstFlag == 1" class="popup-experience-immediately">
        <div class="main-content">
            <div class="info">
                <br>
                30
            </div>
            <a @click="getData.firstFlag = 0" class="link" href="#"></a>
        </div>
        <div class="bg-content"></div>
    </div>
    <!--  -->
    <div v-if="succeed" class="popup-invite-friends">
        <div class="main-content">
            <span @click="goToHome" class="close"></span>
            <div class="i"></div>
            <div class="t">
                !
            </div>
            <div class="info">
                 <span class="b">{{saveMoney}}</span> !
            </div>
            <a @click="goToGoodFriendsShare" class="link" href="javascript:void(0)"></a>
        </div>
        <div class="bg-content"></div>
    </div>
  </div>
</template>

<script>
import wx from 'weixin-js-sdk'
export default {
    name: 'home',
    data(){
        return {
            getData: '',
            succeed: false,
            saveMoney: '',
            thumb: require ('@/assets/logo.png'),
            detail: ''
        }
    },
    methods:{
        goToWX(){
            this.$util.postRequest('/walletpn/invite/wxConfig')
            .then( response => {
                let res = JSON.parse(JSON.stringify(response))
                res = res.data;
                this.wxInit(res);
                console.log

                console.log(res)
            })
        },
        getDitall(){
            this.$util.postRequest('/walletpn/invite/wxShare')
            .then( response => {
                console.log(1)
                console.log(response)
                this.detail = response.data
                this.goToWX()
            })
        },
        // 
        wxInit(res) {
            let imgUrl = location.protocol + location.host + this.thumb;
            wx.config({
                debug: false,
                appId: res.appId,
                timestamp: res.timestamp,
                nonceStr: res.nonceStr,
                signature: res.signature,
                jsApiList: ['onMenuShareTimeline', 'onMenuShareAppMessage', 'onMenuShareQQ', 'onMenuShareQZone']
            });
            wx.ready(()=> {
                //
                wx.onMenuShareTimeline({
                    title: this.detail.title, // 
                    desc: this.detail.desc, // 
                    link: this.detail.link, // 
                    imgUrl: imgUrl, // 
                    success: function() {
                        // alert("")
                    },
                    cancel: function() {
                        alert(",!")
                    }
                }); 
                //
                wx.onMenuShareAppMessage({
                    title: this.detail.title, // 
                    desc: this.detail.desc, // 
                    link: this.detail.link, // 
                    imgUrl: imgUrl, // 
                    success: function() {
                        // alert("")
                    },
                    cancel: function() {
                        alert(",!")
                    }
                });
                //QQ
                wx.onMenuShareQQ({
                    title: this.detail.title, // 
                    desc: this.detail.desc, // 
                    link: this.detail.link, // 
                    imgUrl: imgUrl, // 
                    success: function() {
                        // alert("QQ")
                    },
                    cancel: function() {
                        alert(",!")
                    }
                });
                //QQ
                wx.onMenuShareQZone({
                    title: this.detail.title, // 
                    desc: this.detail.desc, // 
                    link: this.detail.link, // 
                    imgUrl: imgUrl, // 
                    success: function() {
                        // alert("QQ")
                    },
                    cancel: function() {
                        alert(",!")
                    }
                });
            });
            wx.error(function(err) {
                alert(JSON.stringify(err))
            });
        },
        getInfo(){
            this.$util.postRequest('/walletpn/LoginEnter/firstPageData')
            .then( res => {
                console.log(res)
                this.getData = res.data
            })
            .catch(function (error) {
                console.log(error);
            });
        },
        goToHome(){
            this.succeed = false
            this.$router.push({ name: 'home'})
        },
        goToGoodFriendsShare(){
            this.succeed = false
            this.$router.push({ name: 'home'})
            this.$router.push({ name: 'goodFriendsShare'})
        }
    },
    computed:{
        isSucceed(){
            let discount =  this.$route.query.discount
            if(discount != null&discount != ''&this.$store.getters.getDiscount == 0){
                this.succeed = true
                this.saveMoney = discount
                this.$store.commit('changeDiscount')
            }
            else{
                this.succeed = false
                this.saveMoney = ''
            }
        }
    },
    created(){
        this.getDitall()
        this.getInfo()
        this.isSucceed
    }
}
</script>